$color-primary: #9356dc;
$color-secondary: #ff79da;
$color-tertiaire: #99e2d0;
$color-placeholder-header: #d3d3d3;
$color-back-body: #e5e7e9;
$color-police-text: #000;
$color-body-origine: #fff;
$color-header: #f0f0f2;
$color-shadow: #85929e;
$color-shadow-reservation: #cacfd2;
header.reservation {
  margin-top: 0;
}
.reservation {
  width: 100%;
  height: 260px;
  background-color: var($color-header);
  text-align: center;

  .exploration {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
    .titleexploration {
      font-size: medium;
    }
    .btn-Explorer {
      width: 230px;
      height: 25px;
      background: linear-gradient(var($color-secondary), var($color-primary));
      border-radius: 1.5em;
      padding: 10px 12px 10px 12px;
      color: var($color-body-origine);
      font-size: large;
      text-decoration: none;
      cursor: pointer;
      box-shadow: 5px 3px 5px var($color-shadow-reservation);
      text-align: center;
      padding-top: 8px;
      margin-bottom: 10px;
    }
  }
}
